<template>
  <div class="content">
    <div class="md-layout md-medium-size-100 md-xsmall-size-100 md-size-3">

      <form
        novalidate
        class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100"
        @submit.prevent="">
        <md-card class="card-mural">

          <md-card-content>
            <ckeditor :editor="editor" v-model="editorData" :config="editorConfig"></ckeditor>
          </md-card-content>

          <md-card-actions>
            <md-button class="md-primary" v-on:click="atualizar(item)">Atualizar</md-button>
          </md-card-actions>
        </md-card>
      </form>


      <div class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-33" >

      </div>

      <div class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-33" >

      </div>

    </div>

    <div v-if="sending" class="loading"><md-progress-spinner class="md-accent" md-mode="indeterminate"></md-progress-spinner></div>
  </div>
</template>

<script>
import {
  StatsCard,
  ChartCard,
  NavTabsCard,
  NavTabsTable,
  OrderedTable
} from "@/components";

import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

const toBase64 = file => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
});

export default {
  components: {
    StatsCard,
    ChartCard,
    NavTabsCard,
    NavTabsTable,
    OrderedTable
  },
  props: {
  },
  data() {
    return {
      editor:null,
      sending: false,

      editor: ClassicEditor,
      editorData: '<p>Content of the editor.</p>',
      editorConfig: {
        language: 'pt',
        contentsLanguage: 'pt',
        uiColor: '#9AB8F3',

      }
    };
  },
  methods: {
    addLoja: function() {
      const self = this
      self.sending = true
      this.axios({
        method:'POST',
        url:this.$api.baseUrl+'/',
        data:{action:'addLoja', nome:'Nova loja', contato:'Adicionar contato', cupom:''},
        headers:this.$api.headers,
      }).then((response) => {
        self.refresh()
      })
    },
    atualizar: function(item) {
      const self = this
      this.axios({
        method:'POST',
        url:this.$api.baseUrl+'/',
        data:{
          action:'updateLoja',
          nome:item.nome,
          contato:item.contato,
          destaque:item.destaque?1:0,
          cupom:item.cupom,
          imagem: item.imagem,
          vendas: item.vendas,
          id:item.id,
        },
        headers:this.$api.headers,
      }).then((response) => {
        self.refresh()
      })
    },
    confirmApagar: function(item) {
      if(confirm("Apagar: " + item.nome)) this.apagar(item)
    },
    apagar: function(item) {
      const self = this
      self.sending = true
      this.axios({
        method:'POST',
        url:this.$api.baseUrl+'/',
        data:{ action:'deleteLoja', id:item.id, },
        headers:this.$api.headers,
      }).then((response) => {
        self.refresh()
      })
    },
    uploadImage: function(item) {
      const self = this
      const _item = item
      let uploader = document.createElement('input')
      uploader.setAttribute('type','file')
      uploader.setAttribute('accept','image/png, image/jpeg')
      uploader.onchange = (e) => {
        self.sending = true
        toBase64(uploader.files[0]).then((data) => {
          this.axios({
            method:'POST',
            url:this.$api.baseUrl+'/',
            data:{
              action:'addMedia',
              data: data,
              idLoja: _item.id
            },
            headers:this.$api.headers,
          }).then((response) => {
            self.refresh()
          })
        })
      }
      uploader.click()
    },
    refresh: function() {
      // lojas
      const self = this
      self.sending = true
      this.axios({
        method:'POST',
        url:this.$api.baseUrl+'/',
        data:{action:'getLojas'},
        headers:this.$api.headers,
      }).then((response) => {
        self.lojas = response.data.data.map((item) => {
          item.destaque = item.destaque==1
          return item
        })
        self.sending = false
      })
    },
  },
  mounted() {

  },
  beforeDestroy() {

  },
};
</script>
